import { v4 as uuidv4 } from 'uuid';

// ============= 1. Èõ¢Á∑öÁãÄÊÖãÁÆ°ÁêÜ =============
interface OfflineState {
  isOnline: boolean;
  isInitialSync: boolean;
  lastSyncTime: Date | null;
  pendingChanges: SyncOperation[];
  conflictResolutions: ConflictResolution[];

  setOnline: (status: boolean) => void;
  addPendingChange: (operation: SyncOperation) => void;
  removePendingChange: (id: string) => void;
  clearPendingChanges: () => void;
  setLastSyncTime: (time: Date) => void;
}

export const useOfflineStore = create<OfflineState>()(
  persist(
    (set) => ({
      isOnline: typeof navigator !== 'undefined' ? navigator.onLine : true,
      isInitialSync: false,
      lastSyncTime: null,
      pendingChanges: [],
      conflictResolutions: [],

      setOnline: (status) => set({ isOnline: status }),
      addPendingChange: (operation) => set((state) => ({
        pendingChanges: [...state.pendingChanges, operation]
      })),
      removePendingChange: (id) => set((state) => ({
        pendingChanges: state.pendingChanges.filter(op => op.id !== id)
      })),
      clearPendingChanges: () => set({ pendingChanges: [] }),
      setLastSyncTime: (time) => set({ lastSyncTime: time })
    }),
    {
      name: 'offline-storage',
      partialize: (state) => ({
        pendingChanges: state.pendingChanges,
        lastSyncTime: state.lastSyncTime
      })
    }
  )
);

// ============= 2. ÂêåÊ≠•Êìç‰ΩúÂÆöÁæ© =============
export interface SyncOperation {
  id: string;
  type: 'CREATE' | 'UPDATE' | 'DELETE';
  table: string;
  data: any;
  localId?: string;
  remoteId?: string;
  timestamp: number;
  retryCount: number;
  userId: string;
}

export interface ConflictResolution {
  id: string;
  table: string;
  localData: any;
  remoteData: any;
  resolution: 'LOCAL' | 'REMOTE' | 'MERGE';
  resolvedData?: any;
  timestamp: number;
}

// ============= 3. Èõ¢Á∑öË≥áÊñôÂ∫´ÁÆ°ÁêÜ =============
export class LocalDatabase {
  private dbName = 'venturo-local';
  private version = 2; // ÂçáÁ¥öÁâàÊú¨‰ª•Âä†ÂÖ• visas Ë°®Ê†º
  private db: IDBDatabase | null = null;

  async init(): Promise<void> {
    // Ê™¢Êü•ÊòØÂê¶Âú®ÁÄèË¶ΩÂô®Áí∞Â¢É
    if (typeof window === 'undefined' || !window.indexedDB) {
      console.warn('IndexedDB not available (server-side)');
      return Promise.resolve();
    }

    return new Promise((resolve, reject) => {
      const request = indexedDB.open(this.dbName, this.version);

      request.onerror = () => reject(request.error);
      request.onsuccess = () => {
        this.db = request.result;
        resolve();
      };

      request.onupgradeneeded = (event) => {
        const db = (event.target as IDBOpenDBRequest).result;

        // Âª∫Á´ãÂêÑÂÄãË≥áÊñôË°®ÁöÑ Object Store
        const tables = [
          'tours', 'orders', 'customers', 'payments',
          'members', 'users', 'todos', 'quotes', 'visas'
        ];

        tables.forEach(table => {
          if (!db.objectStoreNames.contains(table)) {
            const store = db.createObjectStore(table, { keyPath: 'id' });
            store.createIndex('sync_status', 'sync_status', { unique: false });
            store.createIndex('updated_at', 'updated_at', { unique: false });
          }
        });

        // Âª∫Á´ãÂêåÊ≠•ÂÖÉË≥áÊñôË°®
        if (!db.objectStoreNames.contains('sync_metadata')) {
          db.createObjectStore('sync_metadata', { keyPath: 'table' });
        }
      };
    });
  }

  async get(table: string, id: string): Promise<any> {
    if (!this.db) await this.init();
    if (!this.db) return null;

    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction([table], 'readonly');
      const store = transaction.objectStore(table);
      const request = store.get(id);

      request.onsuccess = () => resolve(request.result);
      request.onerror = () => reject(request.error);
    });
  }

  async getAll(table: string): Promise<any[]> {
    if (!this.db) await this.init();
    if (!this.db) return [];

    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction([table], 'readonly');
      const store = transaction.objectStore(table);
      const request = store.getAll();

      request.onsuccess = () => resolve(request.result || []);
      request.onerror = () => reject(request.error);
    });
  }

  async put(table: string, data: any): Promise<void> {
    if (!this.db) await this.init();
    if (!this.db) return;

    const record = {
      ...data,
      sync_status: 'pending',
      updated_at: new Date().toISOString(),
      local_updated: true
    };

    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction([table], 'readwrite');
      const store = transaction.objectStore(table);
      const request = store.put(record);

      request.onsuccess = () => resolve();
      request.onerror = () => reject(request.error);
    });
  }

  async delete(table: string, id: string): Promise<void> {
    if (!this.db) await this.init();
    if (!this.db) return;

    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction([table], 'readwrite');
      const store = transaction.objectStore(table);
      const request = store.delete(id);

      request.onsuccess = () => resolve();
      request.onerror = () => reject(request.error);
    });
  }

  async getUnsyncedRecords(table: string): Promise<any[]> {
    if (!this.db) await this.init();
    if (!this.db) return [];

    return new Promise((resolve, reject) => {
      const transaction = this.db!.transaction([table], 'readonly');
      const store = transaction.objectStore(table);
      const index = store.index('sync_status');
      const request = index.getAll('pending');

      request.onsuccess = () => resolve(request.result || []);
      request.onerror = () => reject(request.error);
    });
  }
}

// ============= 4. ÂêåÊ≠•ÁÆ°ÁêÜÂô® =============
export class SyncManager {
  private localDb: LocalDatabase;
  private syncQueue: SyncOperation[] = [];
  private isSyncing = false;
  private syncInterval: NodeJS.Timeout | null = null;

  constructor() {
    this.localDb = new LocalDatabase();
    // Âè™Âú®ÁÄèË¶ΩÂô®Áí∞Â¢ÉÂàùÂßãÂåñ
    if (typeof window !== 'undefined') {
      this.init();
    }
  }

  private async init() {
    await this.localDb.init();

    // Áõ£ËÅΩÁ∂≤Ë∑ØÁãÄÊÖãËÆäÂåñ
    if (typeof window !== 'undefined') {
      window.addEventListener('online', () => this.handleOnline());
      window.addEventListener('offline', () => this.handleOffline());
    }

    // ‰∏çÂïüÂãïÂÆöÊúüÂêåÊ≠•ÔºåÂè™Âú®ÊúâËÆäÊõ¥ÊôÇÂêåÊ≠•
    // this.startPeriodicSync();
  }

  private handleOnline = async () => {
    console.log('üü¢ Á∂≤Ë∑ØÂ∑≤ÈÄ£Êé•ÔºåÈñãÂßãÂêåÊ≠•...');
    useOfflineStore.getState().setOnline(true);
    await this.syncAll();
  };

  private handleOffline = () => {
    console.log('üî¥ Á∂≤Ë∑ØÂ∑≤Êñ∑ÈñãÔºåÂàáÊèõÂà∞Èõ¢Á∑öÊ®°Âºè');
    useOfflineStore.getState().setOnline(false);
  };

  private startPeriodicSync() {
    this.syncInterval = setInterval(() => {
      if (useOfflineStore.getState().isOnline && !this.isSyncing) {
        this.syncPendingChanges();
      }
    }, 5000); // ÊîπÁÇ∫ 5 ÁßíÔºåÊõ¥Âç≥ÊôÇÁöÑÂêåÊ≠•
  }

  // ============= Ê®ÇËßÄÊõ¥Êñ∞ =============
  async optimisticCreate(table: string, data: any): Promise<any> {
    const tempId = `temp_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;

    // ËôïÁêÜÈúÄË¶Å‰º∫ÊúçÂô®Á∑®ËôüÁöÑÊ¨Ñ‰Ωç
    const record = {
      ...data,
      id: tempId,
      // Â¶ÇÊûúÊòØË®ÇÂñÆÔºå‰ΩøÁî®„ÄåÂæÖÁ∑®Ëôü„Äç
      ...(table === 'orders' && { orderNumber: 'ÂæÖÁ∑®Ëôü' }),
      // Â¶ÇÊûúÊòØÊóÖÈÅäÂúòÔºå‰ΩøÁî®„ÄåÂæÖÁ∑®Ëôü„Äç
      ...(table === 'tours' && { code: 'ÂæÖÁ∑®Ëôü' }),
      // Â¶ÇÊûúÊòØÂ†±ÂÉπÂñÆÔºå‰ΩøÁî®„ÄåÂæÖÁ∑®Ëôü„Äç
      ...(table === 'quotes' && { quoteNumber: 'ÂæÖÁ∑®Ëôü' }),
      syncStatus: 'pending',
      isOfflineDraft: true // Ê®ôË®òÁÇ∫Èõ¢Á∑öËçâÁ®ø
    };

    await this.localDb.put(table, record);

    const operation: SyncOperation = {
      id: uuidv4(),
      type: 'CREATE',
      table,
      data, // ‰øùÂ≠òÂéüÂßãË≥áÊñôÔºà‰∏çÂê´Ëá®ÊôÇÊ¨Ñ‰ΩçÔºâ
      localId: tempId,
      timestamp: Date.now(),
      retryCount: 0,
      userId: data.created_by || 'unknown'
    };

    useOfflineStore.getState().addPendingChange(operation);

    if (useOfflineStore.getState().isOnline) {
      this.syncOperation(operation);
    }

    return record;
  }

  async optimisticUpdate(table: string, id: string, updates: any): Promise<any> {
    const existing = await this.localDb.get(table, id);
    const updated = { ...existing, ...updates };

    await this.localDb.put(table, updated);

    const operation: SyncOperation = {
      id: uuidv4(),
      type: 'UPDATE',
      table,
      data: { id, ...updates },
      remoteId: id,
      timestamp: Date.now(),
      retryCount: 0,
      userId: updates.updated_by || 'unknown'
    };

    useOfflineStore.getState().addPendingChange(operation);

    if (useOfflineStore.getState().isOnline) {
      this.syncOperation(operation);
    }

    return updated;
  }

  async optimisticDelete(table: string, id: string): Promise<void> {
    await this.localDb.put(table, {
      id,
      deleted: true,
      deleted_at: new Date().toISOString()
    });

    const operation: SyncOperation = {
      id: uuidv4(),
      type: 'DELETE',
      table,
      data: { id },
      remoteId: id,
      timestamp: Date.now(),
      retryCount: 0,
      userId: 'unknown'
    };

    useOfflineStore.getState().addPendingChange(operation);

    if (useOfflineStore.getState().isOnline) {
      this.syncOperation(operation);
    }
  }

  // ============= ÂêåÊ≠•Âü∑Ë°å =============
  private async syncOperation(operation: SyncOperation): Promise<void> {
    // üì¶ Á¥îÊú¨Âú∞Ê®°Âºè - ÂÅúÁî® Supabase ÂêåÊ≠•
    console.log(`üì¶ Êú¨Âú∞Ê®°ÂºèÔºöË∑≥ÈÅéÂêåÊ≠•Êìç‰Ωú ${operation.type} ${operation.table}`);
    useOfflineStore.getState().removePendingChange(operation.id);
    return;

    /* ‚ö†Ô∏è Supabase ÂêåÊ≠•ÂäüËÉΩÂ∑≤ÂÅúÁî®
    try {
      switch (operation.type) {
        case 'CREATE':
          // 1. Â¶ÇÊûúÈúÄË¶Å‰º∫ÊúçÂô®Á∑®ËôüÔºåÂÖàÂèñËôü
          let dataWithNumber = { ...operation.data };

          if (operation.table === 'orders' && !operation.data.orderNumber) {
            const orderNumber = await this.getNextOrderNumber();
            dataWithNumber.orderNumber = orderNumber;
          } else if (operation.table === 'tours' && !operation.data.code) {
            const code = await this.getNextTourCode();
            dataWithNumber.code = code;
          } else if (operation.table === 'quotes' && !operation.data.quoteNumber) {
            const quoteNumber = await this.getNextQuoteNumber();
            dataWithNumber.quoteNumber = quoteNumber;
          }

          // 2. ‰ΩøÁî® VenturoAPI ÊèíÂÖ•ÔºàËá™ÂãïËôïÁêÜ camelCase -> snake_caseÔºâ
          const created = await VenturoAPI.create(operation.table, dataWithNumber);

          // 3. Êõ¥Êñ∞Êú¨Âú∞Ë®òÈåÑÔºöÁî®ÁúüÂØ¶ ID ÂíåÁ∑®ËôüÊõøÊèõËá®ÊôÇÁöÑ
          if (created && operation.localId) {
            await this.updateLocalId(operation.table, operation.localId, created);
          }
          break;

        case 'UPDATE':
          // ‰ΩøÁî® VenturoAPI Êõ¥Êñ∞ÔºàËá™ÂãïËôïÁêÜ camelCase -> snake_caseÔºâ
          if (!operation.data.id) {
            throw new Error('Update operation missing id');
          }
          await VenturoAPI.update(operation.table, operation.data.id, operation.data);
          break;

        case 'DELETE':
          // ‰ΩøÁî® VenturoAPI Âà™Èô§
          if (!operation.remoteId) {
            throw new Error('Delete operation missing remoteId');
          }
          await VenturoAPI.delete(operation.table, operation.remoteId);
          break;
      }

      // ÂêåÊ≠•ÊàêÂäüÔºåÁßªÈô§ÂæÖÂêåÊ≠•È†ÖÁõÆ
      useOfflineStore.getState().removePendingChange(operation.id);

    } catch (error) {
      // Âè™Âú®ÈñãÁôºÊ®°ÂºèÈ°ØÁ§∫ÂêåÊ≠•ÈåØË™§
      if (process.env.NODE_ENV === 'development') {
        console.warn(`‚ö†Ô∏è ÂêåÊ≠•Â§±Êïó [${operation.type}] - Â∞áÁ®çÂæåÈáçË©¶:`, error);
      }

      operation.retryCount++;

      if (operation.retryCount > 3) {
        await this.handleConflict(operation, error);
      }
    }
    */
  }

  private async updateLocalId(table: string, localId: string, remoteData: any) {
    const localRecord = await this.localDb.get(table, localId);
    if (localRecord) {
      // Âà™Èô§Ëá®ÊôÇË®òÈåÑ
      await this.localDb.delete(table, localId);

      // Áî®‰º∫ÊúçÂô®ÁöÑÂÆåÊï¥Ë≥áÊñôÔºàÂåÖÂê´ÁúüÂØ¶ ID ÂíåÁ∑®ËôüÔºâÂª∫Á´ãÊñ∞Ë®òÈåÑ
      await this.localDb.put(table, {
        ...remoteData,
        syncStatus: 'synced',
        isOfflineDraft: false
      });

      console.log(`‚úÖ Â∑≤ÂêåÊ≠• ${table}:`, {
        from: localId,
        to: remoteData.id,
        number: remoteData.orderNumber || remoteData.code || remoteData.quoteNumber
      });
    }
  }

  // ============= Á∑®ËôüÁîüÊàê =============
  private async getNextOrderNumber(): Promise<string> {
    // Âæû Supabase ÂèñÂæóÊúÄÊñ∞ÁöÑË®ÇÂñÆÁ∑®Ëôü
    const { data, error } = await supabase
      .from('orders')
      .select('orderNumber')
      .order('created_at', { ascending: false })
      .limit(1)
      .single();

    if (error || !data) {
      // Â¶ÇÊûúÊ≤íÊúâË≥áÊñôÔºåÂæû ORDER-2024-0001 ÈñãÂßã
      return `ORDER-${new Date().getFullYear()}-0001`;
    }

    // Ëß£ÊûêÁ∑®Ëôü‰∏¶ÈÅûÂ¢û
    const match = data.orderNumber.match(/ORDER-(\d{4})-(\d{4})/);
    if (match) {
      const year = new Date().getFullYear();
      const lastNumber = parseInt(match[2]);
      const newNumber = (lastNumber + 1).toString().padStart(4, '0');
      return `ORDER-${year}-${newNumber}`;
    }

    return `ORDER-${new Date().getFullYear()}-0001`;
  }

  private async getNextTourCode(): Promise<string> {
    const { data, error } = await supabase
      .from('tours')
      .select('code')
      .order('created_at', { ascending: false })
      .limit(1)
      .single();

    if (error || !data) {
      return `TOUR-${new Date().getFullYear()}-0001`;
    }

    const match = data.code.match(/TOUR-(\d{4})-(\d{4})/);
    if (match) {
      const year = new Date().getFullYear();
      const lastNumber = parseInt(match[2]);
      const newNumber = (lastNumber + 1).toString().padStart(4, '0');
      return `TOUR-${year}-${newNumber}`;
    }

    return `TOUR-${new Date().getFullYear()}-0001`;
  }

  private async getNextQuoteNumber(): Promise<string> {
    const { data, error } = await supabase
      .from('quotes')
      .select('quoteNumber')
      .order('created_at', { ascending: false })
      .limit(1)
      .single();

    if (error || !data) {
      return `QUOTE-${new Date().getFullYear()}-0001`;
    }

    const match = data.quoteNumber.match(/QUOTE-(\d{4})-(\d{4})/);
    if (match) {
      const year = new Date().getFullYear();
      const lastNumber = parseInt(match[2]);
      const newNumber = (lastNumber + 1).toString().padStart(4, '0');
      return `QUOTE-${year}-${newNumber}`;
    }

    return `QUOTE-${new Date().getFullYear()}-0001`;
  }

  // ============= Ë°ùÁ™ÅËôïÁêÜ =============
  private async handleConflict(operation: SyncOperation, error: any) {
    console.warn('ÂÅµÊ∏¨Âà∞ÂêåÊ≠•Ë°ùÁ™Å:', operation);

    const conflict: ConflictResolution = {
      id: uuidv4(),
      table: operation.table,
      localData: operation.data,
      remoteData: null,
      resolution: 'LOCAL',
      timestamp: Date.now()
    };

    useOfflineStore.setState((state) => ({
      conflictResolutions: [...state.conflictResolutions, conflict]
    }));
  }

  // ============= ÊâπÈáèÂêåÊ≠• =============
  async syncAll(): Promise<void> {
    if (this.isSyncing) return;

    this.isSyncing = true;

    try {
      await this.pullRemoteChanges();
      await this.pushLocalChanges();

      useOfflineStore.getState().setLastSyncTime(new Date());

      console.log('‚úÖ ÂêåÊ≠•ÂÆåÊàê');

    } catch (error) {
      console.error('ÂêåÊ≠•Â§±Êïó:', error);
    } finally {
      this.isSyncing = false;
    }
  }

  private async pullRemoteChanges() {
    // üì¶ Á¥îÊú¨Âú∞Ê®°Âºè - ÂÅúÁî® Supabase ÂêåÊ≠•
    console.log('üì¶ Êú¨Âú∞Ê®°ÂºèÔºöË∑≥ÈÅéÂæûÈõ≤Á´ØÂêåÊ≠•');
    return;

    /* ‚ö†Ô∏è Supabase ÂêåÊ≠•ÂäüËÉΩÂ∑≤ÂÅúÁî®
    const lastSync = useOfflineStore.getState().lastSyncTime;
    const tables = ['tours', 'orders', 'customers', 'payments', 'todos', 'quotes'];

    for (const table of tables) {
      try {
        let query = supabase.from(table).select('*');

        if (lastSync) {
          query = query.gt('updated_at', lastSync.toISOString());
        }

        const { data, error } = await query;

        if (error) throw error;

        for (const record of data || []) {
          await this.localDb.put(table, {
            ...record,
            sync_status: 'synced'
          });
        }

      } catch (error) {
        console.error(`ÂêåÊ≠• ${table} Â§±Êïó:`, error);
      }
    }
    */
  }

  private async pushLocalChanges() {
    const pendingChanges = useOfflineStore.getState().pendingChanges;

    for (const operation of pendingChanges) {
      await this.syncOperation(operation);
    }
  }

  private async syncPendingChanges() {
    const pendingChanges = useOfflineStore.getState().pendingChanges;

    if (pendingChanges.length === 0) return;

    console.log(`‚è≥ ÂêåÊ≠• ${pendingChanges.length} ÂÄãÂæÖËôïÁêÜËÆäÊõ¥...`);

    // ÂéªÈô§ÈáçË§áÁöÑÊìç‰ΩúÔºàÁõ∏Âêå table + idÔºâ
    const uniqueOperations = pendingChanges.reduce((acc, op) => {
      const key = `${op.table}-${op.data?.id || op.localId}`;
      if (!acc.has(key)) {
        acc.set(key, op);
      }
      return acc;
    }, new Map());

    console.log(`‚úì ÂéªÈô§ÈáçË§áÂæåÔºö${uniqueOperations.size} ÂÄãÂæÖËôïÁêÜËÆäÊõ¥`);

    // ‰∏¶Ë°åËôïÁêÜÊâÄÊúâÂêåÊ≠•Êìç‰ΩúÔºåÊèêÂçáÊïàËÉΩ
    await Promise.allSettled(
      Array.from(uniqueOperations.values()).map(operation => this.syncOperation(operation))
    );
  }

  dispose() {
    if (this.syncInterval) {
      clearInterval(this.syncInterval);
    }
  }

  getLocalDb() {
    return this.localDb;
  }
}

// ============= 5. React Hooks =============
export function useOfflineSync() {
  const { isOnline, pendingChanges, lastSyncTime } = useOfflineStore();

  return {
    isOnline,
    hasPendingChanges: pendingChanges.length > 0,
    pendingCount: pendingChanges.length,
    lastSyncTime,
    syncStatus: !isOnline ? 'offline' :
                pendingChanges.length > 0 ? 'pending' :
                'synced'
  };
}

// ÂñÆ‰æãÊ®°Âºè - Êá∂Âä†ËºâÔºåÂè™Âú®ÁÄèË¶ΩÂô®Áí∞Â¢ÉÂâµÂª∫
let syncManagerInstance: SyncManager | null = null;

export function getSyncManager(): SyncManager {
  if (typeof window === 'undefined') {
    // Server-side: ËøîÂõû mock Â∞çË±°
    return {} as SyncManager;
  }

  if (!syncManagerInstance) {
    syncManagerInstance = new SyncManager();
  }

  return syncManagerInstance;
}

// ÁÇ∫‰∫ÜÂêë‰∏ãÁõ∏ÂÆπÔºå‰øùÁïôËàäÁöÑÂ∞éÂá∫ÊñπÂºè
export const syncManager = typeof window !== 'undefined' ? getSyncManager() : {} as SyncManager;
