'use client';

import { useState, useEffect } from 'react';
import { useAuthStore } from '@/stores/auth-store';
import { useLocalAuthStore } from '@/lib/auth/local-auth-manager';
import { Sidebar } from './sidebar';
import { cn } from '@/lib/utils';
import { usePathname, useRouter } from 'next/navigation';

interface MainLayoutProps {
  children: React.ReactNode;
}

export function MainLayout({ children }: MainLayoutProps) {
  const { sidebarCollapsed } = useAuthStore();
  const { currentProfile } = useLocalAuthStore();
  const pathname = usePathname();
  const router = useRouter();
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    setIsClient(true);
  }, []);

  // Á∞°ÂåñÁöÑË™çË≠âÊ™¢Êü• - Êö´ÊôÇÂÅúÁî®,‰ΩøÁî® auth-store ÁöÑ user
  useEffect(() => {
    if (!isClient) return;
    if (pathname === '/login') return;

    // Áµ¶ Zustand persist ‰∏ÄÈªûÊôÇÈñìËºâÂÖ•
    const checkTimeout = setTimeout(() => {
      const authUser = useAuthStore.getState().user;

      // Êö´ÊôÇÂÅúÁî®Ê™¢Êü•,ÈÅøÂÖçÁÑ°ÈôêÂæ™Áí∞
      // if (!authUser) {
      //   router.push('/login');
      // }
    }, 50);

    return () => clearTimeout(checkTimeout);
  }, [isClient, pathname, currentProfile, router]);

  // ÂàùÂßãÂåñÈõ¢Á∑öË≥áÊñôÂ∫´
  useEffect(() => {
    // Èõ¢Á∑öË≥áÊñôÂ∫´ÊúÉÂú® sync-manager ‰∏≠Ëá™ÂãïÂàùÂßãÂåñ
  }, [isClient]);

  // ‰∏çÈúÄË¶ÅÂÅ¥ÈÇäÊ¨ÑÁöÑÈ†ÅÈù¢
  const noSidebarPages = ['/login', '/unauthorized'];
  const shouldShowSidebar = !noSidebarPages.includes(pathname);

  // ÁôªÂÖ•È†Å‰∏çÈúÄË¶ÅÂÅ¥ÈÇäÊ¨Ñ
  if (!shouldShowSidebar) {
    return (
      <div className="min-h-screen bg-background">
        {children}
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-background">
      {/* Â∑¶‰∏ãË±°Èôê - ÂÅ¥ÈÇäÊ¨Ñ */}
      <Sidebar />

      {/* Âè≥‰∏ãË±°Èôê - ‰∏ªÂÖßÂÆπÂçÄÂüü */}
      <main className={cn(
        'transition-all duration-300 pt-18',
        !isClient ? 'ml-16' : (sidebarCollapsed ? 'ml-16' : 'ml-64')
      )}>
        <div className="h-[calc(100vh-72px)] p-6">
          {children}
        </div>
      </main>
    </div>
  );
}