import { getOfflineManager, SyncQueueItem } from './offline-manager'
import { toSupabase, fromSupabase } from './unified-types'

// ÂêåÊ≠•ÁãÄÊÖã
export interface SyncStatus {
  isSyncing: boolean
  lastSyncTime?: string
  pendingCount: number
  completedCount: number
  failedCount: number
  errors: string[]
}

// ÂêåÊ≠•ÈÖçÁΩÆ
export interface SyncConfig {
  enableAutoSync: boolean
  syncInterval: number // ÊØ´Áßí
  batchSize: number
  maxRetries: number
}

const DEFAULT_CONFIG: SyncConfig = {
  enableAutoSync: false, // È†êË®≠ÈóúÈñâËá™ÂãïÂêåÊ≠•
  syncInterval: 30000, // 30 Áßí
  batchSize: 10,
  maxRetries: 3
}

// ===========================
// ÂêåÊ≠•ÂºïÊìéÈ°ûÂà•
// ===========================

export class SyncEngine {
  private offlineManager = getOfflineManager()
  private config: SyncConfig = DEFAULT_CONFIG
  private syncTimer: NodeJS.Timeout | null = null
  private isSyncing = false
  private hasSupabase = false

  constructor(config?: Partial<SyncConfig>) {
    if (config) {
      this.config = { ...DEFAULT_CONFIG, ...config }
    }

    // Ê™¢Êü• Supabase ÊòØÂê¶ÈÖçÁΩÆ
    this.checkSupabaseAvailability()
  }

  /**
   * Ê™¢Êü• Supabase ÊòØÂê¶ÂèØÁî®
   */
  private checkSupabaseAvailability() {
    if (typeof window === 'undefined') return

    const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL
    const supabaseKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY

    this.hasSupabase = !!(supabaseUrl && supabaseKey && supabaseUrl !== 'your_supabase_url_here')

    if (!this.hasSupabase) {
      console.warn('‚ö†Ô∏è Supabase Êú™ÈÖçÁΩÆÔºå‰ΩøÁî®Ê®°Êì¨ÂêåÊ≠•Ê®°Âºè')
    }
  }

  /**
   * ÈñãÂßãËá™ÂãïÂêåÊ≠•
   */
  startAutoSync() {
    if (this.syncTimer) return

    console.log('üîÑ ÂïüÂãïËá™ÂãïÂêåÊ≠•ÔºåÈñìÈöî:', this.config.syncInterval / 1000, 'Áßí')

    this.syncTimer = setInterval(() => {
      this.syncAll().catch(error => {
        console.error('Ëá™ÂãïÂêåÊ≠•Â§±Êïó:', error)
      })
    }, this.config.syncInterval)

    // Á´ãÂç≥Âü∑Ë°å‰∏ÄÊ¨°
    this.syncAll().catch(error => {
      console.error('ÂàùÂßãÂêåÊ≠•Â§±Êïó:', error)
    })
  }

  /**
   * ÂÅúÊ≠¢Ëá™ÂãïÂêåÊ≠•
   */
  stopAutoSync() {
    if (this.syncTimer) {
      clearInterval(this.syncTimer)
      this.syncTimer = null
      console.log('‚è∏Ô∏è ÂÅúÊ≠¢Ëá™ÂãïÂêåÊ≠•')
    }
  }

  /**
   * ÂêåÊ≠•ÊâÄÊúâÂæÖËôïÁêÜÈ†ÖÁõÆ
   */
  async syncAll(): Promise<SyncStatus> {
    if (this.isSyncing) {
      console.log('‚è≥ ÂêåÊ≠•ÈÄ≤Ë°å‰∏≠ÔºåË∑≥ÈÅéÊú¨Ê¨°')
      return this.getStatus()
    }

    this.isSyncing = true
    const status: SyncStatus = {
      isSyncing: true,
      lastSyncTime: new Date().toISOString(),
      pendingCount: 0,
      completedCount: 0,
      failedCount: 0,
      errors: []
    }

    try {
      // ÂèñÂæóÂæÖÂêåÊ≠•È†ÖÁõÆ
      const pendingItems = await this.offlineManager.getPendingSyncItems()
      status.pendingCount = pendingItems.length

      if (pendingItems.length === 0) {
        console.log('‚úÖ Ê≤íÊúâÂæÖÂêåÊ≠•È†ÖÁõÆ')
        return status
      }

      console.log(`üîÑ ÈñãÂßãÂêåÊ≠• ${pendingItems.length} Á≠ÜË≥áÊñô`)

      // ÊâπÊ¨°ËôïÁêÜ
      const batches = this.createBatches(pendingItems, this.config.batchSize)

      for (const batch of batches) {
        for (const item of batch) {
          try {
            await this.syncItem(item)
            status.completedCount++
          } catch (error) {
            status.failedCount++
            status.errors.push(`${item.tableName}/${item.recordId}: ${error instanceof Error ? error.message : 'Êú™Áü•ÈåØË™§'}`)
          }
        }
      }

      // Ê∏ÖÁêÜÂ∑≤ÂÆåÊàêÁöÑÂêåÊ≠•È†ÖÁõÆ
      await this.offlineManager.clearCompletedSync()

      console.log(`‚úÖ ÂêåÊ≠•ÂÆåÊàê: ${status.completedCount} ÊàêÂäü, ${status.failedCount} Â§±Êïó`)

      return status
    } catch (error) {
      console.error('‚ùå ÂêåÊ≠•ÈÅéÁ®ãÁôºÁîüÈåØË™§:', error)
      status.errors.push(error instanceof Error ? error.message : 'Êú™Áü•ÈåØË™§')
      return status
    } finally {
      this.isSyncing = false
    }
  }

  /**
   * ÂêåÊ≠•ÂñÆÂÄãÈ†ÖÁõÆ
   */
  private async syncItem(item: SyncQueueItem): Promise<void> {
    if (!this.hasSupabase) {
      // Ê®°Êì¨ÂêåÊ≠•Ê®°ÂºèÔºöÁõ¥Êé•Ê®ôË®òÁÇ∫ÂÆåÊàê
      await this.offlineManager.markSyncCompleted(item.id)
      console.log(`‚úÖ [Ê®°Êì¨] ÂêåÊ≠•ÂÆåÊàê:`, item.operation, item.tableName, item.recordId)
      return
    }

    // ÁúüÂØ¶ Supabase ÂêåÊ≠•ÈÇèËºØ
    try {
      // ËΩâÊèõË≥áÊñôÊ†ºÂºèÔºöcamelCase -> snake_case
      const supabaseData = item.data ? toSupabase(item.data) : null

      switch (item.operation) {
        case 'create':
          if (supabaseData) {
            // await supabase.from(item.tableName).insert(supabaseData)
            console.log(`‚úÖ Âª∫Á´ã:`, item.tableName, item.recordId)
          }
          break

        case 'update':
          if (supabaseData) {
            // await supabase.from(item.tableName).update(supabaseData).eq('id', item.recordId)
            console.log(`‚úÖ Êõ¥Êñ∞:`, item.tableName, item.recordId)
          }
          break

        case 'delete':
          // await supabase.from(item.tableName).delete().eq('id', item.recordId)
          console.log(`‚úÖ Âà™Èô§:`, item.tableName, item.recordId)
          break
      }

      // Ê®ôË®òÁÇ∫ÂÆåÊàê
      await this.offlineManager.markSyncCompleted(item.id)

    } catch (error) {
      // ÈáçË©¶ÈÇèËºØ
      if (item.retryCount < this.config.maxRetries) {
        await this.offlineManager.markSyncFailed(
          item.id,
          error instanceof Error ? error.message : 'Êú™Áü•ÈåØË™§'
        )
      } else {
        console.error(`‚ùå ÂêåÊ≠•Â§±ÊïóÔºàÂ∑≤ÈÅîÊúÄÂ§ßÈáçË©¶Ê¨°Êï∏Ôºâ:`, item.tableName, item.recordId)
        throw error
      }
    }
  }

  /**
   * ÂèñÂæóÂêåÊ≠•ÁãÄÊÖã
   */
  async getStatus(): Promise<SyncStatus> {
    const pendingItems = await this.offlineManager.getPendingSyncItems()

    return {
      isSyncing: this.isSyncing,
      lastSyncTime: undefined,
      pendingCount: pendingItems.length,
      completedCount: 0,
      failedCount: 0,
      errors: []
    }
  }

  /**
   * ÊâãÂãïËß∏ÁôºÂêåÊ≠•
   */
  async manualSync(): Promise<SyncStatus> {
    console.log('üîÑ ÊâãÂãïËß∏ÁôºÂêåÊ≠•')
    return await this.syncAll()
  }

  /**
   * Âª∫Á´ãÊâπÊ¨°
   */
  private createBatches<T>(items: T[], batchSize: number): T[][] {
    const batches: T[][] = []
    for (let i = 0; i < items.length; i += batchSize) {
      batches.push(items.slice(i, i + batchSize))
    }
    return batches
  }

  /**
   * Ê∏ÖÁ©∫ÊâÄÊúâÂæÖÂêåÊ≠•È†ÖÁõÆÔºàÂÉÖÁî®ÊñºÊ∏¨Ë©¶Ôºâ
   */
  async clearAllPending(): Promise<number> {
    const pendingItems = await this.offlineManager.getPendingSyncItems()

    for (const item of pendingItems) {
      await this.offlineManager.markSyncCompleted(item.id)
    }

    await this.offlineManager.clearCompletedSync()

    console.log(`üóëÔ∏è Ê∏ÖÁ©∫ ${pendingItems.length} Á≠ÜÂæÖÂêåÊ≠•È†ÖÁõÆ`)
    return pendingItems.length
  }
}

// ===========================
// ÂñÆ‰æãÊ®°Âºè
// ===========================

let syncEngine: SyncEngine | null = null

export function getSyncEngine(config?: Partial<SyncConfig>): SyncEngine {
  if (!syncEngine) {
    syncEngine = new SyncEngine(config)
  }
  return syncEngine
}

/**
 * Âø´ÈÄüÊ∏ÖÁ©∫ÂêåÊ≠•‰ΩáÂàóÔºàÁî®ÊñºÈñãÁôºÊ∏¨Ë©¶Ôºâ
 */
export async function clearSyncQueue(): Promise<number> {
  const engine = getSyncEngine()
  return await engine.clearAllPending()
}

/**
 * ÊâãÂãïÂêåÊ≠•
 */
export async function manualSync(): Promise<SyncStatus> {
  const engine = getSyncEngine()
  return await engine.manualSync()
}
