import { getOfflineDB } from './offline-database';
import type { StoreName } from './offline-database';
import { generateUUID, toSupabase, fromSupabase } from './unified-types';

// Re-export StoreName for convenience
export type { StoreName } from './offline-database';

// ===========================
// ÂêåÊ≠•‰ΩáÂàóÈ†ÖÁõÆ
// ===========================

export interface SyncQueueItem {
  id: string;
  operation: 'create' | 'update' | 'delete';
  tableName: StoreName;
  recordId: string;
  data?: any;
  createdAt: string;
  status: 'pending' | 'processing' | 'completed' | 'failed';
  retryCount: number;
  error?: string;
}

// ===========================
// Èõ¢Á∑öÁÆ°ÁêÜÂô®È°ûÂà•
// ===========================

export class OfflineManager {
  private db = getOfflineDB();
  private localStorage = typeof window !== 'undefined' ? window.localStorage : null;

  /**
   * Âª∫Á´ãË≥áÊñô (Êñ∞Â¢û)
   */
  async create<T extends Record<string, any>>(
    storeName: StoreName,
    data: Partial<T>
  ): Promise<T> {
    const now = new Date().toISOString();
    const record: any = {
      ...data,
      id: generateUUID(),
      createdAt: now,
      updatedAt: now,
      synced: false,
      lastSyncedAt: undefined,
      syncError: undefined,
      version: 1
    };

    // ÂÑ≤Â≠òÂà∞ IndexedDB
    await this.db.add(storeName, record);

    // Âä†ÂÖ•ÂêåÊ≠•‰ΩáÂàó
    await this.addToSyncQueue('create', storeName, record.id, record);

    console.log(`‚úÖ Âª∫Á´ãË≥áÊñô:`, storeName, record.id);
    return record as T;
  }

  /**
   * Êõ¥Êñ∞Ë≥áÊñô
   */
  async update<T extends Record<string, any>>(
    storeName: StoreName,
    id: string,
    updates: Partial<T>
  ): Promise<T> {
    // ËÆÄÂèñÁèæÊúâË≥áÊñô
    const existing = await this.db.get<any>(storeName, id);
    if (!existing) {
      throw new Error(`Êâæ‰∏çÂà∞Ë≥áÊñô: ${id}`);
    }

    // Âêà‰ΩµÊõ¥Êñ∞
    const updated: any = {
      ...existing,
      ...updates,
      updatedAt: new Date().toISOString(),
      synced: false,
      version: (existing.version || 1) + 1
    };

    // ÂÑ≤Â≠òÂà∞ IndexedDB
    await this.db.update(storeName, updated);

    // Âä†ÂÖ•ÂêåÊ≠•‰ΩáÂàó
    await this.addToSyncQueue('update', storeName, id, updated);

    console.log(`‚úÖ Êõ¥Êñ∞Ë≥áÊñô:`, storeName, id);
    return updated as T;
  }

  /**
   * Âà™Èô§Ë≥áÊñô
   */
  async delete(storeName: StoreName, id: string): Promise<void> {
    // Âæû IndexedDB Âà™Èô§
    await this.db.delete(storeName, id);

    // Âä†ÂÖ•ÂêåÊ≠•‰ΩáÂàó
    await this.addToSyncQueue('delete', storeName, id);

    console.log(`‚úÖ Âà™Èô§Ë≥áÊñô:`, storeName, id);
  }

  /**
   * ËÆÄÂèñÂñÆÁ≠ÜË≥áÊñô
   */
  async get<T>(storeName: StoreName, id: string): Promise<T | null> {
    return await this.db.get<T>(storeName, id);
  }

  /**
   * ËÆÄÂèñÊâÄÊúâË≥áÊñô
   */
  async getAll<T>(storeName: StoreName): Promise<T[]> {
    return await this.db.getAll<T>(storeName);
  }

  /**
   * ‰ΩøÁî®Á¥¢ÂºïÊü•Ë©¢
   */
  async getByIndex<T>(
    storeName: StoreName,
    indexName: string,
    value: any
  ): Promise<T[]> {
    return await this.db.getByIndex<T>(storeName, indexName, value);
  }

  /**
   * ÊâπÊ¨°Âª∫Á´ã
   */
  async createBatch<T extends Record<string, any>>(
    storeName: StoreName,
    dataList: Partial<T>[]
  ): Promise<T[]> {
    const now = new Date().toISOString();
    const records: any[] = dataList.map(data => ({
      ...data,
      id: generateUUID(),
      createdAt: now,
      updatedAt: now,
      synced: false,
      version: 1
    }));

    // ÊâπÊ¨°ÂÑ≤Â≠òÂà∞ IndexedDB
    await this.db.addBatch(storeName, records);

    // ÊâπÊ¨°Âä†ÂÖ•ÂêåÊ≠•‰ΩáÂàó
    for (const record of records) {
      await this.addToSyncQueue('create', storeName, record.id, record);
    }

    console.log(`‚úÖ ÊâπÊ¨°Âª∫Á´ã ${records.length} Á≠ÜË≥áÊñô:`, storeName);
    return records as T[];
  }

  /**
   * Ê∏ÖÁ©∫ Store
   */
  async clear(storeName: StoreName): Promise<void> {
    await this.db.clear(storeName);
    console.log(`‚úÖ Ê∏ÖÁ©∫:`, storeName);
  }

  /**
   * ÂèñÂæóË≥áÊñôÊï∏Èáè
   */
  async count(storeName: StoreName): Promise<number> {
    return await this.db.count(storeName);
  }

  // ===========================
  // ÂêåÊ≠•‰ΩáÂàóÁÆ°ÁêÜ
  // ===========================

  /**
   * Âä†ÂÖ•ÂêåÊ≠•‰ΩáÂàó
   */
  private async addToSyncQueue(
    operation: SyncQueueItem['operation'],
    tableName: StoreName,
    recordId: string,
    data?: any
  ): Promise<void> {
    const queueItem: SyncQueueItem = {
      id: generateUUID(),
      operation,
      tableName,
      recordId,
      data,
      createdAt: new Date().toISOString(),
      status: 'pending',
      retryCount: 0
    };

    await this.db.add('syncQueue', queueItem);
    console.log(`üì§ Âä†ÂÖ•ÂêåÊ≠•‰ΩáÂàó:`, operation, tableName, recordId);
  }

  /**
   * ÂèñÂæóÂæÖÂêåÊ≠•È†ÖÁõÆ
   */
  async getPendingSyncItems(): Promise<SyncQueueItem[]> {
    return await this.db.getByIndex<SyncQueueItem>('syncQueue', 'status', 'pending');
  }

  /**
   * Ê®ôË®òÂêåÊ≠•È†ÖÁõÆÁÇ∫ÂÆåÊàê
   */
  async markSyncCompleted(queueItemId: string): Promise<void> {
    const item = await this.db.get<SyncQueueItem>('syncQueue', queueItemId);
    if (item) {
      item.status = 'completed';
      await this.db.update('syncQueue', item);

      // ÂêåÊôÇÊ®ôË®òÂéüÂßãË≥áÊñôÁÇ∫Â∑≤ÂêåÊ≠•
      await this.db.markAsSynced(item.tableName, item.recordId);
    }
  }

  /**
   * Ê®ôË®òÂêåÊ≠•È†ÖÁõÆÁÇ∫Â§±Êïó
   */
  async markSyncFailed(queueItemId: string, error: string): Promise<void> {
    const item = await this.db.get<SyncQueueItem>('syncQueue', queueItemId);
    if (item) {
      item.status = 'failed';
      item.error = error;
      item.retryCount += 1;
      await this.db.update('syncQueue', item);
    }
  }

  /**
   * Ê∏ÖÁ©∫Â∑≤ÂÆåÊàêÁöÑÂêåÊ≠•‰ΩáÂàó
   */
  async clearCompletedSync(): Promise<void> {
    const completed = await this.db.getByIndex<SyncQueueItem>(
      'syncQueue',
      'status',
      'completed'
    );

    for (const item of completed) {
      await this.db.delete('syncQueue', item.id);
    }

    console.log(`‚úÖ Ê∏ÖÁ©∫ ${completed.length} Á≠ÜÂ∑≤ÂÆåÊàêÂêåÊ≠•È†ÖÁõÆ`);
  }

  // ===========================
  // localStorage ËºîÂä©ÊñπÊ≥ï
  // ===========================

  /**
   * ÂÑ≤Â≠òË®≠ÂÆöÂà∞ localStorage
   */
  saveSetting(key: string, value: any): void {
    if (!this.localStorage) return;
    try {
      this.localStorage.setItem(key, JSON.stringify(value));
    } catch (error) {
      console.error('localStorage ÂÑ≤Â≠òÂ§±Êïó:', error);
    }
  }

  /**
   * Âæû localStorage ËÆÄÂèñË®≠ÂÆö
   */
  getSetting<T>(key: string, defaultValue?: T): T | null {
    if (!this.localStorage) return defaultValue || null;
    try {
      const value = this.localStorage.getItem(key);
      return value ? JSON.parse(value) : (defaultValue || null);
    } catch (error) {
      console.error('localStorage ËÆÄÂèñÂ§±Êïó:', error);
      return defaultValue || null;
    }
  }

  /**
   * Âà™Èô§ localStorage Ë®≠ÂÆö
   */
  removeSetting(key: string): void {
    if (!this.localStorage) return;
    this.localStorage.removeItem(key);
  }

  // ===========================
  // ÁãÄÊÖãÊü•Ë©¢
  // ===========================

  /**
   * ÂèñÂæóÈõ¢Á∑öË≥áÊñôÁµ±Ë®à
   */
  async getStats() {
    const stats: Record<string, number> = {};

    const storeNames: StoreName[] = [
      'tours',
      'orders',
      'quotes',
      'paymentRequests',
      'todos',
      'suppliers',
      'customers'
    ];

    for (const storeName of storeNames) {
      stats[storeName] = await this.db.count(storeName);
    }

    const pendingSync = await this.getPendingSyncItems();
    stats.pendingSync = pendingSync.length;

    return stats;
  }

  /**
   * Ê™¢Êü•ÊòØÂê¶ÊúâÊú™ÂêåÊ≠•Ë≥áÊñô
   */
  async hasUnsyncedData(): Promise<boolean> {
    const pending = await this.getPendingSyncItems();
    return pending.length > 0;
  }
}

// ===========================
// ÂñÆ‰æãÊ®°Âºè
// ===========================

let offlineManager: OfflineManager | null = null;

export function getOfflineManager(): OfflineManager {
  if (!offlineManager) {
    offlineManager = new OfflineManager();
  }
  return offlineManager;
}
