/**
 * üóÑÔ∏è Venturo v4.0 - IndexedDB Èõ¢Á∑öË≥áÊñôÂ∫´Â∞ÅË£ù
 *
 * ÂäüËÉΩÔºö
 * - Â∞ÅË£ù IndexedDB Êìç‰Ωú
 * - Êèê‰æõ CRUD ‰ªãÈù¢
 * - ÊîØÊè¥ÊâπÊ¨°Êìç‰Ωú
 * - Ëá™ÂãïÁ¥¢ÂºïÁÆ°ÁêÜ
 */

import { generateUUID } from './unified-types';

// ===========================
// Ë≥áÊñôÂ∫´ÈÖçÁΩÆ
// ===========================

export const DB_CONFIG = {
  name: 'VenturoOfflineDB',
  version: 1,
  stores: {
    tours: {
      keyPath: 'id',
      indexes: ['code', 'status', 'startDate', 'synced']
    },
    orders: {
      keyPath: 'id',
      indexes: ['orderNumber', 'tourId', 'customerId', 'status', 'synced']
    },
    quotes: {
      keyPath: 'id',
      indexes: ['quoteNumber', 'customerId', 'status', 'synced']
    },
    paymentRequests: {
      keyPath: 'id',
      indexes: ['requestNumber', 'tourId', 'supplierId', 'status', 'synced']
    },
    todos: {
      keyPath: 'id',
      indexes: ['priority', 'completed', 'assigneeId', 'synced']
    },
    suppliers: {
      keyPath: 'id',
      indexes: ['name', 'category', 'status', 'synced']
    },
    customers: {
      keyPath: 'id',
      indexes: ['name', 'phone', 'email', 'synced']
    },
    syncQueue: {
      keyPath: 'id',
      indexes: ['operation', 'tableName', 'createdAt', 'status']
    }
  }
} as const;

// Export StoreName type
export type StoreName = keyof typeof DB_CONFIG.stores;

// ===========================
// IndexedDB Â∞ÅË£ùÈ°ûÂà•
// ===========================

export class OfflineDatabase {
  private db: IDBDatabase | null = null;
  private initPromise: Promise<void> | null = null;

  constructor() {
    this.initPromise = this.init();
  }

  /**
   * ÂàùÂßãÂåñË≥áÊñôÂ∫´
   */
  private async init(): Promise<void> {
    // Ê™¢Êü•ÊòØÂê¶Âú®ÁÄèË¶ΩÂô®Áí∞Â¢É
    if (typeof window === 'undefined' || typeof indexedDB === 'undefined') {
      console.warn('‚ö†Ô∏è IndexedDB ‰∏çÂèØÁî®ÔºàÈùûÁÄèË¶ΩÂô®Áí∞Â¢ÉÔºâ')
      return Promise.resolve()
    }

    return new Promise((resolve, reject) => {
      const request = indexedDB.open(DB_CONFIG.name, DB_CONFIG.version);

      request.onerror = () => {
        console.error('‚ùå IndexedDB ÂàùÂßãÂåñÂ§±Êïó:', request.error);
        reject(request.error);
      };

      request.onsuccess = () => {
        this.db = request.result;
        console.log('‚úÖ IndexedDB ÂàùÂßãÂåñÊàêÂäü');
        resolve();
      };

      request.onupgradeneeded = (event) => {
        const db = (event.target as IDBOpenDBRequest).result;

        // Âª∫Á´ãÊâÄÊúâ Object Stores ÂíåÁ¥¢Âºï
        Object.entries(DB_CONFIG.stores).forEach(([storeName, config]) => {
          if (!db.objectStoreNames.contains(storeName)) {
            const objectStore = db.createObjectStore(storeName, {
              keyPath: config.keyPath,
              autoIncrement: false
            });

            // Âª∫Á´ãÁ¥¢Âºï
            config.indexes.forEach(indexName => {
              objectStore.createIndex(indexName, indexName, { unique: false });
            });

            console.log(`‚úÖ Âª∫Á´ã ObjectStore: ${storeName}`);
          }
        });
      };
    });
  }

  /**
   * Á¢∫‰øùË≥áÊñôÂ∫´Â∑≤ÂàùÂßãÂåñ
   */
  private async ensureDB(): Promise<IDBDatabase> {
    if (!this.db) {
      await this.initPromise;
    }
    if (!this.db) {
      throw new Error('Ë≥áÊñôÂ∫´Êú™ÂàùÂßãÂåñ');
    }
    return this.db;
  }

  /**
   * Êñ∞Â¢ûË≥áÊñô
   */
  async add<T extends { id: string }>(storeName: StoreName, data: T): Promise<T> {
    const db = await this.ensureDB();

    return new Promise((resolve, reject) => {
      const transaction = db.transaction(storeName, 'readwrite');
      const objectStore = transaction.objectStore(storeName);

      // Á¢∫‰øùÊúâ ID
      if (!data.id) {
        data.id = generateUUID();
      }

      const request = objectStore.add(data);

      request.onsuccess = () => {
        console.log(`‚úÖ Êñ∞Â¢ûË≥áÊñôÂà∞ ${storeName}:`, data.id);
        resolve(data);
      };

      request.onerror = () => {
        console.error(`‚ùå Êñ∞Â¢ûË≥áÊñôÂ§±Êïó:`, request.error);
        reject(request.error);
      };
    });
  }

  /**
   * Êõ¥Êñ∞Ë≥áÊñô
   */
  async update<T extends { id: string }>(storeName: StoreName, data: T): Promise<T> {
    const db = await this.ensureDB();

    return new Promise((resolve, reject) => {
      const transaction = db.transaction(storeName, 'readwrite');
      const objectStore = transaction.objectStore(storeName);
      const request = objectStore.put(data);

      request.onsuccess = () => {
        console.log(`‚úÖ Êõ¥Êñ∞Ë≥áÊñô ${storeName}:`, data.id);
        resolve(data);
      };

      request.onerror = () => {
        console.error(`‚ùå Êõ¥Êñ∞Ë≥áÊñôÂ§±Êïó:`, request.error);
        reject(request.error);
      };
    });
  }

  /**
   * Âà™Èô§Ë≥áÊñô
   */
  async delete(storeName: StoreName, id: string): Promise<void> {
    const db = await this.ensureDB();

    return new Promise((resolve, reject) => {
      const transaction = db.transaction(storeName, 'readwrite');
      const objectStore = transaction.objectStore(storeName);
      const request = objectStore.delete(id);

      request.onsuccess = () => {
        console.log(`‚úÖ Âà™Èô§Ë≥áÊñô ${storeName}:`, id);
        resolve();
      };

      request.onerror = () => {
        console.error(`‚ùå Âà™Èô§Ë≥áÊñôÂ§±Êïó:`, request.error);
        reject(request.error);
      };
    });
  }

  /**
   * ÂèñÂæóÂñÆÁ≠ÜË≥áÊñô
   */
  async get<T>(storeName: StoreName, id: string): Promise<T | null> {
    const db = await this.ensureDB();

    return new Promise((resolve, reject) => {
      const transaction = db.transaction(storeName, 'readonly');
      const objectStore = transaction.objectStore(storeName);
      const request = objectStore.get(id);

      request.onsuccess = () => {
        resolve(request.result || null);
      };

      request.onerror = () => {
        console.error(`‚ùå ËÆÄÂèñË≥áÊñôÂ§±Êïó:`, request.error);
        reject(request.error);
      };
    });
  }

  /**
   * ÂèñÂæóÊâÄÊúâË≥áÊñô
   */
  async getAll<T>(storeName: StoreName): Promise<T[]> {
    const db = await this.ensureDB();

    return new Promise((resolve, reject) => {
      const transaction = db.transaction(storeName, 'readonly');
      const objectStore = transaction.objectStore(storeName);
      const request = objectStore.getAll();

      request.onsuccess = () => {
        resolve(request.result || []);
      };

      request.onerror = () => {
        console.error(`‚ùå ËÆÄÂèñÊâÄÊúâË≥áÊñôÂ§±Êïó:`, request.error);
        reject(request.error);
      };
    });
  }

  /**
   * ‰ΩøÁî®Á¥¢ÂºïÊü•Ë©¢
   */
  async getByIndex<T>(
    storeName: StoreName,
    indexName: string,
    value: any
  ): Promise<T[]> {
    const db = await this.ensureDB();

    return new Promise((resolve, reject) => {
      const transaction = db.transaction(storeName, 'readonly');
      const objectStore = transaction.objectStore(storeName);
      const index = objectStore.index(indexName);
      const request = index.getAll(value);

      request.onsuccess = () => {
        resolve(request.result || []);
      };

      request.onerror = () => {
        console.error(`‚ùå Á¥¢ÂºïÊü•Ë©¢Â§±Êïó:`, request.error);
        reject(request.error);
      };
    });
  }

  /**
   * ÊâπÊ¨°Êñ∞Â¢û
   */
  async addBatch<T extends { id: string }>(
    storeName: StoreName,
    data: T[]
  ): Promise<T[]> {
    const db = await this.ensureDB();

    return new Promise((resolve, reject) => {
      const transaction = db.transaction(storeName, 'readwrite');
      const objectStore = transaction.objectStore(storeName);
      const results: T[] = [];

      transaction.oncomplete = () => {
        console.log(`‚úÖ ÊâπÊ¨°Êñ∞Â¢û ${data.length} Á≠ÜË≥áÊñôÂà∞ ${storeName}`);
        resolve(results);
      };

      transaction.onerror = () => {
        console.error(`‚ùå ÊâπÊ¨°Êñ∞Â¢ûÂ§±Êïó:`, transaction.error);
        reject(transaction.error);
      };

      data.forEach(item => {
        if (!item.id) {
          item.id = generateUUID();
        }
        objectStore.add(item);
        results.push(item);
      });
    });
  }

  /**
   * Ê∏ÖÁ©∫ Store
   */
  async clear(storeName: StoreName): Promise<void> {
    const db = await this.ensureDB();

    return new Promise((resolve, reject) => {
      const transaction = db.transaction(storeName, 'readwrite');
      const objectStore = transaction.objectStore(storeName);
      const request = objectStore.clear();

      request.onsuccess = () => {
        console.log(`‚úÖ Ê∏ÖÁ©∫ ${storeName}`);
        resolve();
      };

      request.onerror = () => {
        console.error(`‚ùå Ê∏ÖÁ©∫Â§±Êïó:`, request.error);
        reject(request.error);
      };
    });
  }

  /**
   * ÂèñÂæóË≥áÊñôÊï∏Èáè
   */
  async count(storeName: StoreName): Promise<number> {
    const db = await this.ensureDB();

    return new Promise((resolve, reject) => {
      const transaction = db.transaction(storeName, 'readonly');
      const objectStore = transaction.objectStore(storeName);
      const request = objectStore.count();

      request.onsuccess = () => {
        resolve(request.result);
      };

      request.onerror = () => {
        console.error(`‚ùå Ë®àÊï∏Â§±Êïó:`, request.error);
        reject(request.error);
      };
    });
  }

  /**
   * Êü•Ë©¢Êú™ÂêåÊ≠•ÁöÑË≥áÊñô
   */
  async getUnsyncedData<T>(storeName: StoreName): Promise<T[]> {
    return this.getByIndex<T>(storeName, 'synced', false);
  }

  /**
   * Ê®ôË®òÁÇ∫Â∑≤ÂêåÊ≠•
   */
  async markAsSynced(storeName: StoreName, id: string): Promise<void> {
    const data = await this.get<any>(storeName, id);
    if (data) {
      data.synced = true;
      data.lastSyncedAt = new Date().toISOString();
      await this.update(storeName, data);
    }
  }

  /**
   * ÈóúÈñâË≥áÊñôÂ∫´ÈÄ£Êé•
   */
  close(): void {
    if (this.db) {
      this.db.close();
      this.db = null;
      console.log('‚úÖ IndexedDB ÈÄ£Êé•Â∑≤ÈóúÈñâ');
    }
  }
}

// ===========================
// ÂñÆ‰æãÊ®°Âºè
// ===========================

let dbInstance: OfflineDatabase | null = null;

export function getOfflineDB(): OfflineDatabase {
  if (!dbInstance) {
    dbInstance = new OfflineDatabase();
  }
  return dbInstance;
}
