import { type ClassValue, clsx } from "clsx"
import { twMerge } from "tailwind-merge"
import { format } from "date-fns"

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

export function generateTourCode(location: string, date: Date, isSpecial = false): string {
  if (isSpecial) {
    const dateStr = format(date, 'yyMMdd')
    return `SPC${dateStr}01`
  }

  const locationCodes: Record<string, string> = {
    'Tokyo': 'TYO',
    'Okinawa': 'OKA',
    'Osaka': 'OSA',
    'Kyoto': 'KYO',
    'Hokkaido': 'HOK',
  }

  const locationCode = locationCodes[location] || 'UNK'
  const dateStr = format(date, 'yyMMdd')
  const sequence = '01' // ÂØ¶ÈöõÊáâË©≤ÂæûË≥áÊñôÂ∫´Êü•Ë©¢Áï∂Â§©ÂêåÂú∞ÈªûÂ∑≤ÊúâÊï∏Èáè

  return `${locationCode}${dateStr}${sequence}`
}

// Ê†πÊìöË∫´ÂàÜË≠âÂ≠óËôüÂà§Êñ∑ÊÄßÂà• (ÂÉÖÂè∞ÁÅ£Ë∫´ÂàÜË≠âÂ≠óËôü)
export function getGenderFromIdNumber(idNumber: string): 'M' | 'F' | '' {
  if (!idNumber) return '';

  // Ê™¢Êü•ÊòØÂê¶ÁÇ∫Âè∞ÁÅ£Ë∫´ÂàÜË≠âÂ≠óËôüÊ†ºÂºè
  if (!validateIdNumber(idNumber)) {
    // ÈùûÂè∞ÁÅ£Ë∫´ÂàÜË≠âÂ≠óËôüÔºåË∑≥Âá∫ÈÄöÁü•
    if (typeof window !== 'undefined' && idNumber.length > 0) {
      alert('Ê≠§Ë∫´ÂàÜË≠âÂ≠óËôüÊ†ºÂºè‰∏çÁ¨¶ÂêàÂè∞ÁÅ£Ë∫´ÂàÜË≠âÁ≥ªÁµ±ÔºåË´ãÊâãÂãïËº∏ÂÖ•ÊÄßÂà•');
    }
    return '';
  }

  const secondDigit = idNumber.charAt(1);
  // Á¨¨‰∫åÁ¢ºÁÇ∫1Ë°®Á§∫Áî∑ÊÄßÔºå2Ë°®Á§∫Â•≥ÊÄß
  if (secondDigit === '1') {
    return 'M';
  } else if (secondDigit === '2') {
    return 'F';
  }
  return '';
}

// Ê†πÊìöÁîüÊó•ÂíåÂõûÂúòÊó•ÊúüË®àÁÆóÂπ¥ÈΩ°
export function calculateAge(birthday: string, departureDate: string, returnDate?: string): number {
  if (!birthday || !departureDate) return 0;

  const birthDate = new Date(birthday);
  // ÂÑ™ÂÖà‰ΩøÁî®ÂõûÂúòÊó•ÊúüÔºåÂ¶ÇÊûúÊ≤íÊúâÂâá‰ΩøÁî®Âá∫ÁôºÊó•Êúü
  const referenceDate = returnDate ? new Date(returnDate) : new Date(departureDate);

  let age = referenceDate.getFullYear() - birthDate.getFullYear();
  const monthDiff = referenceDate.getMonth() - birthDate.getMonth();

  // Â¶ÇÊûúÈÇÑÊ≤íÂà∞ÁîüÊó•ÔºåÂπ¥ÈΩ°Ê∏õ1
  if (monthDiff < 0 || (monthDiff === 0 && referenceDate.getDate() < birthDate.getDate())) {
    age--;
  }

  return age;
}

// È©óË≠âË∫´ÂàÜË≠âÂ≠óËôüÊ†ºÂºè
export function validateIdNumber(idNumber: string): boolean {
  if (!idNumber) return false;

  // Âè∞ÁÅ£Ë∫´ÂàÜË≠âÊ†ºÂºèÔºöËã±ÊñáÂ≠óÊØç + 9‰ΩçÊï∏Â≠ó
  const pattern = /^[A-Z]\d{9}$/;
  return pattern.test(idNumber);
}

// È©óË≠âË≠∑ÁÖßËôüÁ¢ºÊ†ºÂºè
export function validatePassportNumber(passportNumber: string): boolean {
  if (!passportNumber) return false;

  // Âè∞ÁÅ£Ë≠∑ÁÖßÊ†ºÂºèÔºöÊï∏Â≠ó + Ëã±ÊñáÂ≠óÊØçÔºåÁ∏ΩÈï∑8-9‰Ωç
  const pattern = /^[0-9]{8,9}$|^[A-Z0-9]{8,9}$/;
  return pattern.test(passportNumber);
}