'use client'

/**
 * üîÑ Venturo v4.0 - Ëá™ÂãïÂêåÊ≠• Provider
 *
 * ÂäüËÉΩÔºö
 * - Âú®ÊáâÁî®ÂïüÂãïÊôÇËá™ÂãïÂàùÂßãÂåñÂêåÊ≠•ÂºïÊìé
 * - Áõ£ËÅΩÁ∂≤Ë∑ØÁãÄÊÖãÔºåÁ∂≤Ë∑ØÊÅ¢Âæ©ÊôÇËá™ÂãïÂêåÊ≠•
 * - ÂÆöÊúüËÉåÊôØÂêåÊ≠•ÔºàÂèØÈÖçÁΩÆÔºâ
 * - Êèê‰æõÂêåÊ≠•ÁãÄÊÖãÁµ¶Êï¥ÂÄãÊáâÁî®
 */

import { createContext, useContext, useEffect, useState, ReactNode } from 'react'
import { getSyncEngine, SyncStatus } from './sync-engine'
import { getOfflineManager } from './offline-manager'

interface AutoSyncContextValue {
  syncStatus: SyncStatus | null
  isOnline: boolean
  triggerSync: () => Promise<void>
  enableAutoSync: () => void
  disableAutoSync: () => void
}

const AutoSyncContext = createContext<AutoSyncContextValue>({
  syncStatus: null,
  isOnline: true,
  triggerSync: async () => {},
  enableAutoSync: () => {},
  disableAutoSync: () => {}
})

export function useAutoSync() {
  return useContext(AutoSyncContext)
}

interface AutoSyncProviderProps {
  children: ReactNode
  enabled?: boolean // ÊòØÂê¶ÂïüÁî®Ëá™ÂãïÂêåÊ≠•ÔºåÈ†êË®≠ true
  interval?: number // ÂêåÊ≠•ÈñìÈöîÔºàÊØ´ÁßíÔºâÔºåÈ†êË®≠ 30000 (30Áßí)
}

export function AutoSyncProvider({
  children,
  enabled = true,
  interval = 30000
}: AutoSyncProviderProps) {
  const [syncStatus, setSyncStatus] = useState<SyncStatus | null>(null)
  const [isOnline, setIsOnline] = useState(true)
  const [autoSyncEnabled, setAutoSyncEnabled] = useState(enabled)
  const [syncEngine] = useState(() => getSyncEngine({
    enableAutoSync: enabled,
    syncInterval: interval
  }))

  // Áõ£ËÅΩÁ∂≤Ë∑ØÁãÄÊÖã
  useEffect(() => {
    if (typeof window === 'undefined') return

    const handleOnline = async () => {
      console.log('üåê Á∂≤Ë∑ØÂ∑≤ÊÅ¢Âæ©ÔºåÊ∫ñÂÇôÂêåÊ≠•...')
      setIsOnline(true)

      // Á∂≤Ë∑ØÊÅ¢Âæ©ÊôÇÁ´ãÂç≥ÂêåÊ≠•
      try {
        const status = await syncEngine.manualSync()
        setSyncStatus(status)
      } catch (error) {
        console.error('Á∂≤Ë∑ØÊÅ¢Âæ©ÂæåÂêåÊ≠•Â§±Êïó:', error)
      }
    }

    const handleOffline = () => {
      console.log('üì¥ Á∂≤Ë∑ØÂ∑≤Êñ∑Á∑öÔºåÂàáÊèõÂà∞Èõ¢Á∑öÊ®°Âºè')
      setIsOnline(false)
    }

    window.addEventListener('online', handleOnline)
    window.addEventListener('offline', handleOffline)

    // ÂàùÂßãÁãÄÊÖã
    setIsOnline(navigator.onLine)

    return () => {
      window.removeEventListener('online', handleOnline)
      window.removeEventListener('offline', handleOffline)
    }
  }, [syncEngine])

  // Ëá™ÂãïÂêåÊ≠•Ê©üÂà∂
  useEffect(() => {
    if (!autoSyncEnabled) return

    console.log('üîÑ ÂïüÂãïËá™ÂãïÂêåÊ≠•Ê©üÂà∂')
    syncEngine.startAutoSync()

    return () => {
      console.log('‚è∏Ô∏è ÂÅúÊ≠¢Ëá™ÂãïÂêåÊ≠•Ê©üÂà∂')
      syncEngine.stopAutoSync()
    }
  }, [autoSyncEnabled, syncEngine])

  // ÂÆöÊúüÊõ¥Êñ∞ÂêåÊ≠•ÁãÄÊÖã
  useEffect(() => {
    const updateStatus = async () => {
      try {
        const status = await syncEngine.getStatus()
        setSyncStatus(status)
      } catch (error) {
        console.error('Êõ¥Êñ∞ÂêåÊ≠•ÁãÄÊÖãÂ§±Êïó:', error)
      }
    }

    // ÂàùÂßãÂåñÊôÇËºâÂÖ•ÁãÄÊÖã
    updateStatus()

    // ÊØè 5 ÁßíÊõ¥Êñ∞‰∏ÄÊ¨°ÁãÄÊÖã
    const statusInterval = setInterval(updateStatus, 5000)

    return () => clearInterval(statusInterval)
  }, [syncEngine])

  const triggerSync = async () => {
    try {
      const status = await syncEngine.manualSync()
      setSyncStatus(status)
    } catch (error) {
      console.error('ÊâãÂãïÂêåÊ≠•Â§±Êïó:', error)
      throw error
    }
  }

  const enableAutoSync = () => {
    setAutoSyncEnabled(true)
  }

  const disableAutoSync = () => {
    setAutoSyncEnabled(false)
  }

  const contextValue: AutoSyncContextValue = {
    syncStatus,
    isOnline,
    triggerSync,
    enableAutoSync,
    disableAutoSync
  }

  return (
    <AutoSyncContext.Provider value={contextValue}>
      {children}
    </AutoSyncContext.Provider>
  )
}
