import jwt from 'jsonwebtoken';
import bcrypt from 'bcryptjs';

const JWT_SECRET = process.env.JWT_SECRET || 'venturo_app_jwt_secret_key_change_in_production_2024';

export interface AuthPayload {
  id: string;
  employeeNumber: string;
  permissions: string[];
  role: string;
}

// ÁîüÊàê JWT token
export function generateToken(payload: AuthPayload): string {
  try {
    return jwt.sign(payload, JWT_SECRET, {
      expiresIn: '8h',
      issuer: 'venturo-app'
    });
  } catch (error) {
    console.warn('JWT generation failed, using simple token', error);
    // Â¶ÇÊûú JWT Â§±ÊïóÔºå‰ΩøÁî®Á∞°ÂñÆÁöÑÁ∑®Á¢ºÊñπÊ°à
    return btoa(JSON.stringify({
      ...payload,
      exp: Date.now() + 8 * 60 * 60 * 1000, // 8Â∞èÊôÇ
      iss: 'venturo-app'
    }));
  }
}

// È©óË≠â JWT token
export function verifyToken(token: string): AuthPayload | null {
  try {
    // ÂòóË©¶ JWT È©óË≠â
    const decoded = jwt.verify(token, JWT_SECRET) as AuthPayload;
    return decoded;
  } catch (jwtError) {
    try {
      // Â¶ÇÊûú JWT Â§±ÊïóÔºåÂòóË©¶Á∞°ÂñÆËß£Á¢º
      const decoded = JSON.parse(atob(token));

      // Ê™¢Êü•ÊòØÂê¶ÈÅéÊúü
      if (decoded.exp && Date.now() > decoded.exp) {
        console.log('Token expired');
        return null;
      }

      return decoded as AuthPayload;
    } catch (fallbackError) {
      console.error('Token verification failed:', fallbackError);
      return null;
    }
  }
}

// Âä†ÂØÜÂØÜÁ¢º
export async function hashPassword(password: string): Promise<string> {
  const saltRounds = 12;
  return bcrypt.hash(password, saltRounds);
}

// È©óË≠âÂØÜÁ¢º
export async function verifyPassword(password: string, hashedPassword: string): Promise<boolean> {
  return bcrypt.compare(password, hashedPassword);
}

// Âæû token ÂèñÂæóÁî®Êà∂Ë≥áË®ä
export function getUserFromToken(token: string): AuthPayload | null {
  return verifyToken(token);
}

// Ê™¢Êü•Ê¨äÈôê
export function hasPermission(userPermissions: string[], requiredPermission: string): boolean {
  return userPermissions.includes(requiredPermission) ||
         userPermissions.includes('super_admin') ||
         userPermissions.includes('admin');
}

// Ê™¢Êü•ËßíËâ≤
export function hasRole(userPermissions: string[], requiredRoles: string[]): boolean {
  return requiredRoles.some(role => userPermissions.includes(role));
}